.about-project {
    padding: 110px 70px;
}

.section__heading {
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: -0.04em;
    border-bottom: 1px solid #000000;
    height: 50px;
    margin: 0 0 70px 0;
}

.about-project__info {
    display: flex;
    justify-content: space-between;
}

.about-project__info_card {
    margin-bottom: 110px;
}

.about-project__info_card:not(:last-child) {
    margin-right: 50px;
}

.about-project__info-heading {
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: -0.04em;
    margin: 0 0 26px 0;
}

.about-project__info-text {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    margin: 0;
}

.about-project__steps {
    display: grid;
    grid-template-columns: repeat(6, auto);
    grid-template-rows: repeat(2, auto);
}

.about-project__steps-text {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    padding: 9px 0;
    margin: 0;
}

.about-project__steps-text_type_one-week {
    grid-column-start: 1;
    grid-column-end: 2;
    color: black;
    background: #2be080;
}

.about-project__steps-text_type_four-week {
    grid-column-start: 2;
    grid-column-end: 7;
    background: #f2f2f2;
}
.about-project__description-text {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    color: #a0a0a0;
    margin: 14px 0 0 0;
}
.about-project__description-text-back {
    grid-column-start: 1;
    grid-column-end: 2;
}

.about-project__description-text-front {
    grid-column-start: 2;
    grid-column-end: 7;
}
@media screen and (max-width: 1279px) {
    .about-project {
        max-width: 768px;
        margin: 0 auto;
        padding: 90px 50px;
    }
    .about-project__info_card {
        margin-bottom: 93px;
    }
    .about-project__info-heading {
        margin-bottom: 22px;
    }
    .about-project__info_card:not(:last-child) {
        margin-right: 30px;
    }
}
@media screen and (max-width: 767px) {
    .about-project {
        max-width: 320px;
        margin: 0 auto;
        padding: 70px 18px;
    }
    .about-project__info-text {
        max-width: 284px;
    }
    .about-project__info_card {
        max-width: 284px;
        margin-bottom: 56px;
    }
    .section__heading {
        font-size: 18px;
        line-height: 22px;
        margin-bottom: 60px;
    }
    .about-project__info {
        flex-direction: column;
    }
    .about-project__info_card:not(:last-child) {
        margin-right: 0;
    }
    .about-project__info_card:last-child {
        margin-bottom: 60px;
    }
}
