body {
    font-family: "Inter", "Arial", sans-serif;
    margin: 0;
    
}
.page {
    max-width: 1280px;
    margin: auto;
}
@media screen and (max-width: 1279px) {
    .page{
        max-width: 768px;
    }
}
@media screen and (max-width: 767px) {
    .page{
        max-width: 320px;
    }
}
