.about-me {
    padding: 110px 70px 100px 70px;
}

.about-me__description {
    display: flex;
    justify-content: space-between;
    margin-bottom: 100px;
}

.about-me__description-title {
    font-style: normal;
    font-weight: normal;
    font-size: 50px;
    line-height: 58px;
    letter-spacing: -0.04em;
    margin: 0 0 18px 0;
}

.about-me__description-subtitle {
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 20px;
    margin: 0 0 26px 0;
}

.about-me__description-paragraph {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 22px;
    margin: 0 0 99px 0;
    max-width: 600px;
    margin-right: 49px;
}

.about-me__description-link {
    text-decoration: none;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    margin-right: 20px;
    transition: opacity 0.3s;
}

.about-me__description-link:hover {
    opacity: 0.7;
}

.about-me__description-photo {
    border-radius: 10px;
    width: 300px;
    height: 327px;
}
@media screen and (max-width: 1279px) {
    .about-me__description-photo{
        width: 255;
        height: 307px;
    }
}

@media(max-width: 767px) {
    .about-me{ 
        max-width: 320px;
    margin: 0 auto;
    padding: 70px 18px;
    background: white;
    }
    .about-me__description {
        flex-direction: column-reverse;
        margin-bottom: 0;
    }
    .about-me__description-photo{
        width: 292px;
        height: 352px;
    }
    .about-me {
        padding: 70px 14px 69px 14px;
    }
    .about-me__description-text{
        padding: 0;
        padding-top: 40px;

    }
    .about-me__description-title{
        margin-bottom: 20px;
        font-size: 30px;
        line-height: 36px;
    }
    .about-me__description-subtitle{
        margin-bottom: 20px;
        font-size: 11px;
        line-height: 16px;
    }
    .about-me__description-paragraph{
        max-width: 292px;
        font-size: 11px;
        line-height: 16px;
        margin-bottom: 40px;
    }
}