.footer {
    padding: 79px 70px 20px 70px;
}

.footer__text {
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 16px;
    text-align: center;
    color: #A0A0A0;
    border-bottom: 1px solid #E8E8E8;
    padding-bottom: 20px;
    margin: 0 0 20px 0;
}

.footer__nav {
    display: flex;
    justify-content: space-between;
}

.footer__copyright {
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 16px;
    margin: 0;
}

.footer__links {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
}

.footer__links-item:not(:last-child) {
    margin-right: 20px;
}

.footer__link {
    text-decoration: none;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 16px;
    text-align: right;
    color: #000000;
    transition: opacity 0.3s;
}

.footer__link:hover {
    opacity: 0.5;
}
@media screen and (max-width: 1279px) {
    .footer {
        max-width: 708px;
        padding: 20px 30px;
        padding-top: 79px;
    }
}
@media screen and (max-width: 767px) {
    .footer {
        max-width: 320px;
        padding: 0 10px;
        margin-top: 79px;
    }
    .footer__nav {
        flex-direction: column-reverse;
    }
    .footer__links {
        flex-direction: column;
        text-align: center;
    }
    .footer__links-item:not(:last-child) {
        margin: 0;
        padding-bottom: 12px;
    }
    .footer__links-item {
        font-size: 12px;
        line-height: 15px;
    }
    .footer__copyright {
        text-align: center;
        font-size: 12px;
        line-height: 15px;
        color: #a0a0a0;
        padding-top: 30px;
    }
}
