.promo {
    background-color: #073042;
    padding: 0 70px;
    padding-top: 75px;
    display: flex;
    flex-direction: column;
}
.promo__body {
    display: flex;
    justify-content: space-between;
}
.promo__heading {
    font-style: normal;
    font-weight: normal;
    font-size: 50px;
    line-height: 58px;
    max-width: 640px;
    margin: 0;
    color: #ffffff;
}
.promo__image {
}
.promo__subtitle {
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 16px;

    color: #ffffff;

    mix-blend-mode: normal;
    opacity: 0.5;
    padding-top: 36px;
    max-width: 310px;
}
.promo__button {
    width: 129px;
    height: 36px;
    opacity: 0.6;
    border: 1px solid #ffffff;
    box-sizing: border-box;
    border-radius: 3px;
    background: transparent;
    cursor: pointer;
    padding: 0;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    color: white;
    margin-top: 156px;
    margin-bottom: 85px;
    padding: 10px 20px;
}

@media screen and (max-width: 1279px) {
    .promo__body {
        flex-direction: column-reverse;
    }
    .promo__image {
        width: 310px;
        height: 304px;
        margin: 0 auto;
        padding-top: 65px;
        padding-bottom: 48px;
    }
    .promo__heading {
        font-size: 40px;
        line-height: 50px;
        text-align: center;
        margin: 0 auto;
    }
    .promo__subtitle {
        text-align: center;
        padding-top: 22px;
        max-width: 260px;
        margin: 0 auto;
    }
    .promo__button {
        margin: 92px auto 0;
        margin-bottom: 66px;
    }
}
@media screen and (max-width: 767px) {
    .promo {
        padding: 0 10px;
        padding-top: 70px;
    }
    .promo__image {
        width: 210px;
        height: 206px;
        padding: 0;
        padding-bottom: 44px;
    }
    .promo__heading {
        max-width: 300px;
        font-size: 30px;
        line-height: 38px;
        text-align: center;
        letter-spacing: -0.04em;
    }
    .promo__subtitle {
        font-size: 13px;
        line-height: 16px;
        padding-top: 24px;
    }
    .promo__button {
        margin-top: 80px;
        margin-bottom: 30px;
    }
}
