.movies__list-poster {
    width: 270px;
    height: 151px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.movies__list-item{
    display: flex;
    width: 1140px;
    background-color: #F9F9F9;
    justify-content: space-between;
}

.movies__list-description {
    margin: 14px 0 5px 0;
    min-height: 36px;
    padding-left: 22px;
}

.movies__list-title {
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    margin: 0;
}

.movies__list-like-button {
    background: url('../../images/like_inactive.svg') center no-repeat;
    background-color: #F9F9F9;
    border: 1px solid transparent;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    outline: none;
    padding: 0;
    transition: border-color 0.3s;
}

.movies__list-like-button:hover {
    cursor: pointer;
}

.movies__list-like-button_clicked {
    background: url('../../images/like_active.svg') center no-repeat;
    background-color: #F9F9F9;
}

.movies__list-delete-button {
    background: url('../../images/delete.svg') center no-repeat;
    background-color: #F9F9F9;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    border: none;
    outline: none;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.3s, opacity 0.3s;
}

.movies__list-delete-button_visible {
    visibility: visible;
    opacity: 1;
    cursor: pointer;
}

.movies__list-duration {
    font-style: normal;
    font-weight: normal;
    font-size: 11px;
    line-height: 13px;
    color: #A0A0A0;
    margin: 0;
    padding-top: 13px;
    padding-bottom: 30px;
}
@media screen and (max-width: 1279px) {
    .movies{
        padding: 30px 60px 80px 40px;
    }
    .movies__list-item{
        max-width: 708px;
        
    }
    .movies__list-poster {
        width: 220px;
        height: 122px;
    }
}
@media screen and (max-width: 767px) {
    .movies{
        max-width: 320px;
        padding: 40px 10px 20px 10px;
    }
    .movies__list {
        grid-template-columns: repeat(1,300px);
        grid-column-gap: 0;
        grid-row-gap: 20px;
        margin: 0;
        padding-top: 40px;
    }
    .movies__list-item {
        max-width: 300px;
    }
    .movies__list-like-button{
        position: absolute;
        top: 0;
        right: 0;
        padding-right: 26px;
    }
    .movies__list-description{
        position: relative;
        padding-left: 12px;
    }
    .movies__list-item{
        flex-direction: column-reverse;
    }
    .movies__list-poster {
        width: 300px;
        height: 168px;
    }
    .movies__list-title{
        font-size: 12px;
        line-height: 15px;
    }
    .movies__list-duration{
        padding-top: 8px;
        padding-bottom: 14px;
    }
    .movies__more-button{
        width: 240px;
    }
    .movies__list-delete-button_visible{
        position: absolute;
        top: 0;
        right: 0;
        padding-right: 26px;
    }
}
